
.CategoriList{
    display: flex;
    background-color: #2196F3;
    padding: 10px;
    
  }
  .CategoriItem {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 1rem;
    padding: 2rem;
    
    border-radius: 5rem;
    box-shadow: 0px 1rem 1rem rgba(0,0, 0, 5);
    background: goldenrod;
    
  }
  .CategoriItem:hover{
    background: linear-gradient(#f20f0f 30%, #3140ec ,orange );
  }
  
  .CategoriItem p{
    font-size: 1.5rem;
    color: blue;
    margin-top: 5rem;
  }
   .CategoriItem img{
    width: 300px;
   }
 
  .CategoriItem h3{
    font-size: 2rem;
    color: rgb(95, 16, 116);
    margin-top: 5rem;
  }
  .CategoriItem a{
    font-size: 2rem;
    color: rgb(95, 16, 116);
    margin-top: 5rem;
  }
  .ShowAndHide {
    white-space: nowrap; 
    width: 300px; 
    overflow: hidden;
    text-overflow: ellipsis;
  }
  
  .ShowAndHide:hover {
    white-space: normal; 
    overflow: visible;
    width: 100%; 
  }